/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
import React, { useEffect, useRef, useState } from 'react';
import {
  CalciteLabel,
  CalciteRadioGroup,
  CalciteRadioGroupItem,
  CalciteSlider,
  CalciteSwitch,
} from '@esri/calcite-components-react';
import BasemapGallery from '@arcgis/core/widgets/BasemapGallery';
import VectorTileLayer from '@arcgis/core/layers/VectorTileLayer';

import { basemapChanged, basemapSelected, createAlert, isImagery } from './utils/basemaps';
import { basemaps } from '../../config/config';

import './Basemaps.scss';
export const Basemaps = (props: any) => {
  const ref = useRef<HTMLDivElement>(null);
  const radioGroup = useRef<HTMLCalciteRadioGroupElement>(null);
  const basemapsTab = useRef<HTMLCalciteRadioGroupItemElement>(null);
  const imageryTab = useRef<HTMLCalciteRadioGroupItemElement>(null);
  const esriTab = useRef<HTMLCalciteRadioGroupItemElement>(null);
  const blendSlider = useRef<HTMLCalciteSliderElement>(null);

  const blendSwitch = useRef<HTMLCalciteSwitchElement>(null);

  const basemapGallery = useRef<BasemapGallery>();
  const [imagerySelected, setImagerySelected] = useState(false);
  const [showSlider, setShowSlider] = useState(false);
  const [selectedTab, setSelectedTab] = useState('basemaps');
  const streets = useRef<VectorTileLayer>();

  //const eventId = 0;
  const calciteRadioGroupChanged = (event: any) => {
    const tab = (event as any).detail;
    //setSelectedTab(tab);

    updateStorage('selectedTab', tab);

    basemapSelected(tab, props.view, basemapGallery.current as BasemapGallery, props.default, props.imagery);
  };
  const toggleBlendLayer = (basemap: __esri.Basemap, add: boolean) => {
    debugger;
    if (streets.current && blendSlider.current?.value && add) {
      if (add) {
        streets.current.opacity = (100 - blendSlider.current.value) / 100;
        basemap.baseLayers.add(streets.current as VectorTileLayer);
      }
    } else {
      const layer = basemapGallery.current?.activeBasemap.baseLayers.find((layer) => {
        return (layer as VectorTileLayer)?.portalItem?.id === basemaps.blendLayer;
      }) as any;
      basemap.baseLayers.remove(layer as VectorTileLayer);
    }
  };
  const blendSwitchChanged = (event: any) => {
    setShowSlider(event.detail.switched);
    updateStorage('blend', event.detail.switched);

    toggleBlendLayer((basemapGallery.current as BasemapGallery).activeBasemap, event.detail.switched);
  };
  const blendValueChanged = (event: any) => {
    if (streets.current) {
      streets.current.opacity = (100 - event.target.value) / 100;
    }
  };
  const updateStorage = (key: string, value: any) => {
    let storage = window.localStorage.getItem('imaps_basemaps');
    if (!storage) {
      storage = JSON.stringify({ key: value });
      window.localStorage.setItem('imaps_basemaps', storage);
    } else {
      const item = JSON.parse(storage);
      item[key] = value;
      window.localStorage.setItem('imaps_basemaps', JSON.stringify(item));
    }
  };
  const getGroupId = () => {
    const storage = window.localStorage.getItem('imaps_basemaps');
    if (storage) {
      const item = JSON.parse(storage);
      if (item.selectedTab === 'imagery') {
        return { query: { id: basemaps.imagery.id } };
      }
      if (item.selectedTab === 'basemaps') {
        return { query: { id: basemaps.default.id } };
      }
      if (item.selectedTab === 'esri') {
        return { query: { id: null } };
      }
    } else {
      debugger;
      return { query: { id: basemaps.default.id } };
    }
  };

  const checkBlend = (): boolean => {
    const storage = window.localStorage.getItem('imaps_basemaps');
    if (!storage) {
      return false;
    } else {
      const item = JSON.parse(storage);
      return item.blend;
    }
  };
  const checkIsImagery = (): boolean => {
    const storage = window.localStorage.getItem('imaps_basemaps');
    if (!storage) {
      return false;
    } else {
      const item = JSON.parse(storage);
      return item.isImagery;
    }
  };
  useEffect(() => {
    basemapGallery.current = new BasemapGallery({
      container: ref.current as HTMLDivElement,
      view: props.view,
      source: getGroupId() as any,
    });
    const imagery = checkIsImagery();
    const blend = checkBlend();

    setImagerySelected(imagery);
    setTimeout(() => {
      if (blendSwitch.current) {
        blendSwitch.current.checked = blend;
        setShowSlider(blend && imagery);
      }
    });

    createAlert();
    basemapGallery.current.watch('activeBasemap', (activeBasemap: __esri.Basemap) => {
      const imagery = isImagery(basemapGallery.current as BasemapGallery);
      if (imagery) {
        setSelectedTab('imagery');
        updateStorage('selectedTab', 'imagery');
      }
      setImagerySelected(imagery);

      updateStorage('isImagery', imagery);
      updateStorage('baseMapId', activeBasemap.portalItem.id);
      if (selectedTab) {
        basemapChanged(activeBasemap, props.view, selectedTab);
      }
      if (blendSwitch.current) {
        toggleBlendLayer((basemapGallery.current as BasemapGallery).activeBasemap, blendSwitch.current?.checked);
      }
    });
    basemapGallery.current.when(() => {
      const storage = window.localStorage.getItem('imaps_basemaps');
      if (storage) {
        const item = JSON.parse(storage);
        if (item.baseMapId) {
          console.log(item.baseMapId, basemapGallery.current);
        }
        if (item.selectedTab) {
          basemapSelected(
            item.selectedTab,
            props.view,
            basemapGallery.current as BasemapGallery,
            props.default,
            props.imagery,
          );
        }
      }
      streets.current = basemapGallery.current?.activeBasemap.baseLayers.find((layer) => {
        return (layer as VectorTileLayer)?.portalItem?.id === basemaps.blendLayer;
      }) as any;
      if (!streets.current) {
        streets.current = new VectorTileLayer({
          portalItem: {
            id: basemaps.blendLayer,
          },
          opacity: 0.5,
        });
      }
      if (blendSlider.current && streets.current) {
        blendSlider.current.value = 100 - streets.current.opacity * 100;
      }
    });
    return () => {
      basemapGallery.current && basemapGallery.current.destroy();
    };
  }, []);
  useEffect(() => {
    const storage = window.localStorage.getItem('imaps_basemaps');
    if (storage) {
      const item = JSON.parse(storage);
      if (imageryTab.current) {
        imageryTab.current.checked = item.selectedTab === 'imagery';
      }
      if (basemapsTab.current) {
        basemapsTab.current.checked = item.selectedTab === 'basemaps';
      }
      if (esriTab.current) {
        esriTab.current.checked = item.selectedTab === 'esri';
      }
    } else {
      if (basemapsTab.current) {
        basemapsTab.current.checked = true;
      }
    }
  });
  return (
    <div className="panel">
      <CalciteRadioGroup
        onCalciteRadioGroupChange={calciteRadioGroupChanged}
        appearance="solid"
        layout="horizontal"
        scale="m"
        width="full"
        ref={radioGroup}
      >
        <CalciteRadioGroupItem ref={basemapsTab} value="basemaps">
          Streets
        </CalciteRadioGroupItem>
        <CalciteRadioGroupItem ref={imageryTab} value="imagery">
          Imagery
        </CalciteRadioGroupItem>
        <CalciteRadioGroupItem ref={esriTab} value="esri">
          Esri
        </CalciteRadioGroupItem>
      </CalciteRadioGroup>
      {imagerySelected && (
        <div id="blendDiv">
          <CalciteLabel>
            Blend
            <CalciteSwitch ref={blendSwitch} onCalciteSwitchChange={blendSwitchChanged}></CalciteSwitch>
          </CalciteLabel>
          {showSlider && (
            <CalciteSlider
              ref={blendSlider}
              max={100}
              min={0}
              value={50}
              onCalciteSliderInput={blendValueChanged}
            ></CalciteSlider>
          )}
        </div>
      )}
      <div ref={ref}></div>
    </div>
  );
};
export default Basemaps;
